.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.cat-info {
  column-gap: 30px;
  margin-top: 40px;
  margin-left: 50px;
  display: flex;
}

.cat-img {
  object-fit: cover;
  border-radius: 25px;
  display: block;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, .5);
}

.cat-txt {
  width: 800px;
  flex-direction: column;
  row-gap: 20px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15px;
  display: flex;
}

.cat-temper, .cat-desc {
  font-size: 17px;
  font-weight: bold;
}

.cat-name {
  font-size: 30px;
}

.loader-container {
  width: 400px;
  height: 200px;
  flex-direction: column;
  align-items: center;
  margin: auto;
  font-size: 30px;
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

